@import 'mixins';
@import 'variables';

@import 'global';


/**
 * This is main css file for theme. It is broken into following sections:
 * 1.  Reset
 * 2.  General styles
 * 3.  Animations styles
 * 4.  Templates styles
 * 5.  Grid system styles
 * 6.  Header styles
 * 7.  Left Menu Area styles
 * 8.  Popup menu styles
 * 9.  Slider styles
 * 10. Title styles
 * 11. Blog styles
 * 12. Portfolio styles
 * 13. Shortcodes styles
 * 14. Sidebar styles
 * 15. Side area styles
 * 16. Layer Slider styles
 * 17. Plugins (WordPress) styles
 * 18. Footer styles
 * 19. Contact Form 7
 * 20. Gravity Forms
 */



/* ==========================================================================
   2. General styles
   ========================================================================== */
html {
    height: 100%;
    margin: 0px !important;
    -webkit-transition: all 1.3s ease-out;
    -moz-transition: all 1.3s ease-out;
    -o-transition: all 1.3s ease-out;
    -ms-transition: all 1.3s ease-out;
    transition: all 1.3s ease-out;
}

body {
    font-family: 'Raleway', sans-serif;
    font-size: 13px;
    line-height: 23px;
    color: #8d8d8d;
    font-weight: 400;
    background-color: #f5f5f5;
    overflow-y: scroll;
    overflow-x: hidden !important;
    -webkit-font-smoothing: antialiased;
}

.content ul ul,
.content ol{
    padding: 0 0 0 15px;
}

.wrapper {
    background-color: #f5f5f5;
    position: relative;
    z-index: 1000;
    -webkit-transition: left 0.33s cubic-bezier(0.694, 0.0482, 0.335, 1);
    -moz-transition: left 0.33s cubic-bezier(0.694, 0.0482, 0.335, 1);
    -o-transition: left 0.33s cubic-bezier(0.694, 0.0482, 0.335, 1);
    -ms-transition: left 0.33s cubic-bezier(0.694, 0.0482, 0.335, 1);
    transition: left 0.33s cubic-bezier(0.694, 0.0482, 0.335, 1);
    left: 0px;
}

.right_side_menu_opened .wrapper{
    left: -270px;
}

.wrapper_inner{
    width: 100%;
    overflow: hidden;
}

body.boxed .wrapper_inner{
    overflow: visible;
}

body.boxed .content{
    overflow: hidden;
}

.wp-caption,
.wp-caption-text,
.sticky,
.gallery-caption,
.bypostauthor{
    opacity: 1;
}

.meta {
    display: none;
}

.content{
    margin-top: 0;
    position: relative;
    z-index: 100;
    background-color: #f5f5f5;
}

.content.content_top_margin{
    margin-top: 90px !important;
}

.content.content_top_margin_none{
    margin-top: 0 !important;
}

.container{
    position: relative;
    padding: 0;
    width: 100%;
    z-index:100;
}

.container_inner{
    width: 1100px;
    margin: 0px auto;
}

.content .container {
    background-color: #f5f5f5;
}

.content_bottom .container{
    background-color: transparent !important;
}

.content .container .container_inner.default_template_holder {
    padding-top: 44px;
}

.full_page_container_inner {
    padding: 30px 0 0 0;
}

.full_width {
    position: relative;
    z-index: 100;
    background-color: #f5f5f5;
}

.content_bottom{
    position: relative;
    z-index: 100;
}

/* Ajax loader styles
   ========================================================================== */
.ajax_loader {
    position: fixed;
    top: 50%;
    left: 50%;
    z-index: 1100;
    display: none;
}

@media only screen and (min-width: 1000px){
    body.vertical_menu_enabled .ajax_loader{
        margin-left: 130px;
    }
}

.ajax_loader_1 {
    position: relative;
    display: inline-block;
    width: 100%;
    height: 100%;
}

.ajax_loader_2 {
    margin: -50% 0 0 -50%;
}

/* pulse - start */

.ajax_loader .pulse {
    width: 32px;
    height: 32px;
    margin: -16px 0px 0px -16px;
    background-color: #303030;
    -webkit-border-radius: 16px;
    -moz-border-radius: 16px;
    -ms-border-radius: 16px;
    -o-border-radius: 16px;
    border-radius: 16px;
    -webkit-animation: scaleout 1.0s infinite ease-in-out;
    animation: scaleout 1.0s infinite ease-in-out;
}
@-webkit-keyframes scaleout {

    0% {
        -webkit-transform: scale(0);
    }


    100% {
        -webkit-transform: scale(1);
        opacity: 0;
    }
}
@-moz-keyframes scaleout {

    0% {
        -moz-transform: scale(0);
    }


    100% {
        -moz-transform: scale(1);
        opacity: 0;
    }
}
@-ms-keyframes scaleout {

    0% {
        -ms-transform: scale(0);
    }


    100% {
        -ms-transform: scale(1);
        opacity: 0;
    }
}
@-o-keyframes scaleout {

    0% {
        -o-transform: scale(0);
    }


    100% {
        -o-transform: scale(1);
        opacity: 0;
    }
}
@keyframes scaleout {

    0% {
        transform: scale(0);
        -webkit-transform: scale(0);
    }


    100% {
        transform: scale(1);
        -webkit-transform: scale(1);
        opacity: 0;
    }
}

/* pulse - end */

/* double pulse - start */

.ajax_loader .double_pulse {
    width: 40px;
    height: 40px;
    margin: -20px 0px 0px -20px;
    position: relative;
}

.ajax_loader .double_pulse .double-bounce1, .ajax_loader .double_pulse .double-bounce2 {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #303030;
    opacity: 0.6;
    position: absolute;
    top: 0;
    left: 0;

    -webkit-animation: bounce 2.0s infinite ease-in-out;
    animation: bounce 2.0s infinite ease-in-out;
}

.ajax_loader .double_pulse .double-bounce2 {
    -webkit-animation-delay: -1.0s;
    animation-delay: -1.0s;
}

@-webkit-keyframes bounce {
    0%, 100% { -webkit-transform: scale(0.0) }
    50% { -webkit-transform: scale(1.0) }
}

@keyframes bounce {
    0%, 100% {
        transform: scale(0.0);
        -webkit-transform: scale(0.0);
    } 50% {
          transform: scale(1.0);
          -webkit-transform: scale(1.0);
      }
}

/* double pulse - end */

/* cube - start */

.ajax_loader .cube {
    width: 60px;
    height: 60px;
    margin: -30px 0px 0px -30px;
    background-color: #303030;
    -webkit-animation: rotateplane 1.2s infinite ease-in-out;
    animation: rotateplane 1.2s infinite ease-in-out;
}

@-webkit-keyframes rotateplane {
    0% { -webkit-transform: perspective(120px) }
    50% { -webkit-transform: perspective(120px) rotateY(180deg) }
    100% { -webkit-transform: perspective(120px) rotateY(180deg)  rotateX(180deg) }
}

@keyframes rotateplane {
    0% {
        transform: perspective(120px) rotateX(0deg) rotateY(0deg);
        -webkit-transform: perspective(120px) rotateX(0deg) rotateY(0deg)
    } 50% {
          transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
          -webkit-transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg)
      } 100% {
            transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
            -webkit-transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
        }
}

/* cube - end */

/* rotating cubes - start */

.ajax_loader .rotating_cubes {
    width: 32px;
    height: 32px;
    margin: -16px 0px 0px -16px;
    position: relative;
}

.ajax_loader .rotating_cubes .cube1, .ajax_loader .rotating_cubes .cube2 {
    background-color: #303030;
    width: 10px;
    height: 10px;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-animation: cubemove 1.8s infinite ease-in-out;
    animation: cubemove 1.8s infinite ease-in-out;
}

.ajax_loader .rotating_cubes .cube2 {
    -webkit-animation-delay: -0.9s;
    animation-delay: -0.9s;
}

@-webkit-keyframes cubemove {
    25% { -webkit-transform: translateX(42px) rotate(-90deg) scale(0.5) }
    50% { -webkit-transform: translateX(42px) translateY(42px) rotate(-180deg) }
    75% { -webkit-transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5) }
    100% { -webkit-transform: rotate(-360deg) }
}

@keyframes cubemove {
    25% {
        transform: translateX(42px) rotate(-90deg) scale(0.5);
        -webkit-transform: translateX(42px) rotate(-90deg) scale(0.5);
    } 50% {
          transform: translateX(42px) translateY(42px) rotate(-179deg);
          -webkit-transform: translateX(42px) translateY(42px) rotate(-179deg);
      } 50.1% {
            transform: translateX(42px) translateY(42px) rotate(-180deg);
            -webkit-transform: translateX(42px) translateY(42px) rotate(-180deg);
        } 75% {
              transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
              -webkit-transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
          } 100% {
                transform: rotate(-360deg);
                -webkit-transform: rotate(-360deg);
            }
}

/* rotating cubes - end */

/* stripes - start */

.ajax_loader .stripes {
    width: 50px;
    height: 60px;
    margin: -30px 0px 0px -25px;
    text-align: center;
    font-size: 10px;
}

.ajax_loader .stripes > div {
    background-color: #303030;
    height: 100%;
    width: 6px;
    display: inline-block;
    margin: 0 3px 0 0;

    -webkit-animation: stretchdelay 1.2s infinite ease-in-out;
    animation: stretchdelay 1.2s infinite ease-in-out;
}

.ajax_loader .stripes .rect2 {
    -webkit-animation-delay: -1.1s;
    animation-delay: -1.1s;
}

.ajax_loader .stripes .rect3 {
    -webkit-animation-delay: -1.0s;
    animation-delay: -1.0s;
}

.ajax_loader .stripes .rect4 {
    -webkit-animation-delay: -0.9s;
    animation-delay: -0.9s;
}

.ajax_loader .stripes .rect5 {
    -webkit-animation-delay: -0.8s;
    animation-delay: -0.8s;
}

@-webkit-keyframes stretchdelay {
    0%, 40%, 100% { -webkit-transform: scaleY(0.4) }
    20% { -webkit-transform: scaleY(1.0) }
}

@keyframes stretchdelay {
    0%, 40%, 100% {
        transform: scaleY(0.4);
        -webkit-transform: scaleY(0.4);
    }  20% {
           transform: scaleY(1.0);
           -webkit-transform: scaleY(1.0);
       }
}

/* stripes - end */

/* wave - start */

.ajax_loader .wave {
    width: 72px;
    text-align: center;
    margin: -9px 0px 0px -36px;
}

.ajax_loader .wave > div {
    width: 18px;
    height: 18px;
    background-color: #303030;
    margin: 0px 3px 0px 0px;

    border-radius: 100%;
    display: inline-block;
    -webkit-animation: bouncedelay 1.4s infinite ease-in-out;
    animation: bouncedelay 1.4s infinite ease-in-out;
    /* Prevent first frame from flickering when animation starts */
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

.ajax_loader .wave .bounce1 {
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s;
}

.ajax_loader .wave .bounce2 {
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s;
}

@-webkit-keyframes bouncedelay {
    0%, 80%, 100% { -webkit-transform: scale(0.0) }
    40% { -webkit-transform: scale(1.0) }
}

@keyframes bouncedelay {
    0%, 80%, 100% {
        transform: scale(0.0);
        -webkit-transform: scale(0.0);
    } 40% {
          transform: scale(1.0);
          -webkit-transform: scale(1.0);
      }
}

/* wave - end */

/* two_rotating_circles - start */

.ajax_loader .two_rotating_circles {
    width: 40px;
    height: 40px;
    margin: -20px 0px 0px -20px;
    position: relative;
    text-align: center;

    -webkit-animation: rotatecircles 2.0s infinite linear;
    animation: rotatecircles 2.0s infinite linear;
}

.ajax_loader .two_rotating_circles .dot1, .ajax_loader .two_rotating_circles .dot2 {
    width: 60%;
    height: 60%;
    display: inline-block;
    position: absolute;
    top: 0;
    background-color: #303030;
    border-radius: 100%;

    -webkit-animation: bounce 2.0s infinite ease-in-out;
    animation: bounce 2.0s infinite ease-in-out;
}

.ajax_loader .two_rotating_circles .dot2 {
    top: auto;
    bottom: 0px;
    -webkit-animation-delay: -1.0s;
    animation-delay: -1.0s;
}

@-webkit-keyframes rotatecircles { 100% { -webkit-transform: rotate(360deg) }}

@keyframes rotatecircles { 100% { transform: rotate(360deg); -webkit-transform: rotate(360deg) }}

/* two_rotating_circles - end */

/* five_rotating_circles - start */

.ajax_loader .five_rotating_circles {
    width: 60px;
    height: 60px;
    margin: -30px 0px 0px -30px;
    position: relative;
}

.ajax_loader .five_rotating_circles .container1 > div, .ajax_loader .five_rotating_circles .container2 > div, .ajax_loader .five_rotating_circles .container3 > div {
    width: 12px;
    height: 12px;
    background-color: #303030;
    border-radius: 100%;
    position: absolute;

    -webkit-animation: bouncedelay 1.2s infinite ease-in-out;
    animation: bouncedelay 1.2s infinite ease-in-out;
    /* Prevent first frame from flickering when animation starts */
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

.ajax_loader .five_rotating_circles .spinner-container {
    position: absolute;
    width: 100%;
    height: 100%;
}

.ajax_loader .five_rotating_circles .container2 {
    -webkit-transform: rotateZ(45deg);
    transform: rotateZ(45deg);
}

.ajax_loader .five_rotating_circles .container3 {
    -webkit-transform: rotateZ(90deg);
    transform: rotateZ(90deg);
}

.ajax_loader .five_rotating_circles .circle1 { top: 0; left: 0; }
.ajax_loader .five_rotating_circles .circle2 { top: 0; right: 0; }
.ajax_loader .five_rotating_circles .circle3 { right: 0; bottom: 0; }
.ajax_loader .five_rotating_circles .circle4 { left: 0; bottom: 0; }

.ajax_loader .five_rotating_circles .container2 .circle1 {
    -webkit-animation-delay: -1.1s;
    animation-delay: -1.1s;
}

.ajax_loader .five_rotating_circles .container3 .circle1 {
    -webkit-animation-delay: -1.0s;
    animation-delay: -1.0s;
}

.ajax_loader .five_rotating_circles .container1 .circle2 {
    -webkit-animation-delay: -0.9s;
    animation-delay: -0.9s;
}

.ajax_loader .five_rotating_circles .container2 .circle2 {
    -webkit-animation-delay: -0.8s;
    animation-delay: -0.8s;
}

.ajax_loader .five_rotating_circles .container3 .circle2 {
    -webkit-animation-delay: -0.7s;
    animation-delay: -0.7s;
}

.ajax_loader .five_rotating_circles .container1 .circle3 {
    -webkit-animation-delay: -0.6s;
    animation-delay: -0.6s;
}

.ajax_loader .five_rotating_circles .container2 .circle3 {
    -webkit-animation-delay: -0.5s;
    animation-delay: -0.5s;
}

.ajax_loader .five_rotating_circles .container3 .circle3 {
    -webkit-animation-delay: -0.4s;
    animation-delay: -0.4s;
}

.ajax_loader .five_rotating_circles .container1 .circle4 {
    -webkit-animation-delay: -0.3s;
    animation-delay: -0.3s;
}

.ajax_loader .five_rotating_circles .container2 .circle4 {
    -webkit-animation-delay: -0.2s;
    animation-delay: -0.2s;
}

.ajax_loader .five_rotating_circles .container3 .circle4 {
    -webkit-animation-delay: -0.1s;
    animation-delay: -0.1s;
}

/* five_rotating_circles - end */

/* pulsating_circle - start */

@-webkit-keyframes pulsating_circle {
    0% {
        -webkit-transform: scale(.1);
        transform: scale(.1);
        opacity: 1;
    }
    70% {
        -webkit-transform: scale(1);
        transform: scale(1);
        opacity: .7;
    }
    100% {
        opacity: 0;
    }
}

@keyframes pulsating_circle {
    0% {
        -webkit-transform: scale(.1);
        transform: scale(.1);
        opacity: 1;
    }
    70% {
        -webkit-transform: scale(1);
        transform: scale(1);
        opacity: .7;
    }
    100% {
        opacity: 0;
    }
}

.ajax_loader .pulsating_circle {
    height: 50px;
    width: 50px;
    border-radius: 100%;
    border: 2px solid #303030;
    -webkit-animation: pulsating_circle 1s 0s infinite cubic-bezier(.21,.53,.56,.8);
    animation: pulsating_circle 1s 0s infinite cubic-bezier(.21,.53,.56,.8);
}

/* pulsating_circle - end */

/* ripples - start */

@-webkit-keyframes ripples {
    0% {
        -webkit-transform: scale(.1);
        transform: scale(.1);
        opacity: 1;
    }
    70% {
        -webkit-transform: scale(1);
        transform: scale(1);
        opacity: .7;
    }
    100% {
        opacity: 0;
    }
}

@keyframes ripples {
    0% {
        -webkit-transform: scale(.1);
        transform: scale(.1);
        opacity: 1;
    }
    70% {
        -webkit-transform: scale(1);
        transform: scale(1);
        opacity: .7;
    }
    100% {
        opacity: 0;
    }
}

.ajax_loader .ripples {
    position: relative;
    -webkit-transform: translateX(-25px) translateY(-25px);
    -ms-transform: translateX(-25px) translateY(-25px);
    transform: translateX(-25px) translateY(-25px);
}

.ajax_loader .ripples .ripples_circle {
    position: absolute;
    top: 0;
    left: 0;
    width: 50px;
    height: 50px;
    border-radius: 100%;
    border: 2px solid #303030;
    -webkit-animation: ripples 1.25s 0s infinite cubic-bezier(.21,.53,.56,.8);
    animation: ripples 1.25s 0s infinite cubic-bezier(.21,.53,.56,.8);
}

.ajax_loader .ripples .ripples_circle1 {
    -webkit-animation-delay: 0s;
    animation-delay: 0s;
}

.ajax_loader .ripples .ripples_circle2 {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
}

.ajax_loader .ripples .ripples_circle3 {
    -webkit-animation-delay: 0.4s;
    animation-delay: 0.4s;
}

/* ripples - end */

/* spinner - start */

@-webkit-keyframes spinner {
    from {
        -webkit-transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(359deg);
    }
}

@keyframes spinner {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(359deg);
    }
}

.ajax_loader .spinner {
    height: 80px;
    width: 80px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -40px 0 0 -40px;
    -webkit-animation: spinner 1.5s infinite linear;
    -moz-animation: spinner 1.5s infinite linear;
    -o-animation: spinner 1.5s infinite linear;
    animation: spinner 1.5s infinite linear;
    border: 1px solid #e4e4e4;
    border-radius: 100%;
}
.ajax_loader .spinner:before {
    content: "";
    display: block;
    position: absolute;
    left: -1px;
    top: -1px;
    height: 100%;
    width: 100%;
    border-top: 1px solid #727272;
    border-left: 1px solid transparent;
    border-bottom: 1px solid transparent;
    border-right: 1px solid transparent;
    border-radius: 100%;
}

/* spinner - end */

/* cubes start*/

.loading-center-absolute {
    height: 80px;
    width: 80px;
    margin-top: -40px;
    margin-left: -40px;
}

.loading-center-absolute .object{
    width: 15px;
    height: 15px;
    background-color: #e6ae48;
    margin-right: 15px;
    float: left;
    margin-bottom: 15px;
}

.loading-center-absolute .object_one {
    -webkit-animation: animate 1s -0.9s ease-in-out infinite;
    animation: animate 1s -0.9s ease-in-out infinite;
}

.loading-center-absolute  .object_two {
    -webkit-animation: animate 1s -0.8s ease-in-out infinite;
    animation: animate 1s -0.8s ease-in-out infinite;
}

.loading-center-absolute  .object_three {
    -webkit-animation: animate 1s -0.7s ease-in-out infinite;
    animation: animate 1s -0.7s ease-in-out infinite;
}

.loading-center-absolute  .object_four {
    -webkit-animation: animate 1s -0.6s ease-in-out infinite;
    animation: animate 1s -0.6s ease-in-out infinite;
}

.loading-center-absolute  .object_five {
    -webkit-animation: animate 1s -0.5s ease-in-out infinite;
    animation: animate 1s -0.5s ease-in-out infinite;
}

.loading-center-absolute  .object_six {
    -webkit-animation: animate 1s -0.4s ease-in-out infinite;
    animation: animate 1s -0.4s ease-in-out infinite;
}

.loading-center-absolute  .object_seven {
    -webkit-animation: animate 1s -0.3s ease-in-out infinite;
    animation: animate 1s -0.3s ease-in-out infinite;
}

.loading-center-absolute  .object_eight {
    -webkit-animation: animate 1s -0.2s ease-in-out infinite;
    animation: animate 1s -0.2s ease-in-out infinite;
}

.loading-center-absolute  .object_nine {
    -webkit-animation: animate 1s -0.1s ease-in-out infinite;
    animation: animate 1s -0.1s ease-in-out infinite;
}

.loading-center-absolute  .object:nth-child(3n+0) {
    margin-right: 0px;
}

@-webkit-keyframes animate {


    50% {
        -ms-transform: scale(1.5,1.5);
        -webkit-transform: scale(1.3,1.3);
        transform: scale(1.3,1.3);
    }

    100% {
        -ms-transform: scale(1,1);
        -webkit-transform: scale(1,1);
        transform: scale(1,1);
    }

}

@keyframes animate {
    50% {
        -ms-transform: scale(1.3,1.3);
        -webkit-transform: scale(1.3,1.3);
        transform: scale(1.3,1.3);
    }

    100% {
        -ms-transform: scale(1,1);
        -webkit-transform: scale(1,1);
        transform: scale(1,1);
    }

}

/* cubes end*/

/* indeterminate start */

.indeterminate-holder .indeterminate-holder{
    width:500px;
    margin: 0 auto;
    margin-left:-250px;
}


.indeterminate-holder .indeterminate-progress {
    position: relative;
    height: 4px;
    display: block;
    width: 100%;
    background-color: #E8E6E6;
    overflow: hidden;
}

.indeterminate-holder .indeterminate{
    background-color: #e6ae48;
}

.indeterminate-holder .indeterminate:before {
    content: '';
    position: absolute;
    background-color: inherit;
    top: 0;
    left: 0;
    bottom: 0;
    will-change: left, right;
