/* ==========================================================================
   Unordered list shortcode styles
   ========================================================================== */
.q_list.normal ul > li {
    font-weight: 400 !important;
}

.q_list.light ul > li {
    font-weight: 300 !important;
}

.q_list.bold  ul > li {
    font-weight: 600 !important;
}

.q_list.circle ul,
.q_list.number ul{
    display: block;
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.q_list.number ul{
    counter-reset: li;
}

.q_list.circle ul>li,
.q_list.number ul>li{
    position: relative;
    margin: 0 0 20px;
    padding: 0 0 0 21px;
}

.q_list.number ul>li{
    padding: 0 0 0 32px;
}

.q_list.number.circle_number li {
    padding-left: 45px;
}

.q_list.circle ul>li:before {
    position: absolute;
    left: 0;
    width: 7px;
    height: 7px;
    top: 9px;
    background-color: #e6ae48;
    content: '';
    border-radius: 25px;
}

.q_list.number ul>li:before{
    height: 20px;
    width: 20px;
    line-height: 20px;
    display: inline-block;
    position: absolute;
    left: 0;
    top: 1px;
    padding: 0;
    color: #e6ae48;
    text-align: center;
    content: counter(li, decimal-leading-zero);
    counter-increment: li;
}

.q_list.number.circle_number ul>li:before {
    color: #fff;
    background-color: #e6ae48;
    border-radius: 2em;
    height: 31px;
    width: 31px;
    line-height: 31px;
    top: -3px;
}

.q_list.circle.animate_list ul li,
.q_list.number.animate_list ul li{
    opacity: 0;
    filter: alpha(opacity = 0);
    top: -40px;
}

