/* ==========================================================================
# Interactive Image style - begin
========================================================================== */
.qode-interactive-image {

	position: relative;
	display: inline-block;

	.qode-ii-image-holder img{
		position: relative;
		display: block;
	}

	.qode-ii-info-holder {
		position: relative;
		width: 100%;
		box-sizing: border-box;
		padding: 20px 20px 0;

		.qode-ii-info-holder-inner {
			@include qodeTransition(top 0.3s ease, opacity 0.3s ease, transform 0.3s ease);
			top: 0;
			opacity: 0;
			position: relative;
		}

		.qode-ii-title{
			margin-bottom: 10px;
		}

		.qode-ii-text {
			margin-bottom: 20px;
		}

		.qode-iwoi-link {
		}

	}

	&.active,
	&:hover{
		.qode-ii-info-holder {

			.qode-ii-info-holder-inner {
				top: -40px;
				opacity: 1;
			}
		}
	}

}
/* ==========================================================================
# Interactive Image style - end
========================================================================== */