/* ==========================================================================
# Image With Overlapping Info style - begin
========================================================================== */
.qode-image-with-overlapping-info {

	position: relative;

	.qode-iwoi-image-holder {
		padding-right: 24.5%;
		opacity: 0;
		@include qodeTransition(opacity 0.5s ease-in-out);

		&.qode-appear {
			opacity: 1;
		}
	}

	.qode-iwoi-info-holder {
		position: relative;
		left: calc(91% - 550px);
		margin-top: -21%;
		width: 550px;
		box-sizing: border-box;
		background-color: #fff;
		border: 2px solid #c4c4c4;
		padding: 69px 77px 61px 80px;
		opacity: 0;
		@include qodeTransform(translateY(20%));
		@include qodeTransition(all .6s cubic-bezier(.34,.52,.57,1.04));

		.qode-iwoi-text {
			margin-top: 24px;
		}

		.qode-iwoi-link {
			margin-top: 140px;
		}

		&.qode-appear {
			opacity: 1;
			@include qodeTransform(translateY(0));
		}
	}
}
/* ==========================================================================
# Image With Overlapping Info style - end
========================================================================== */