/* ==========================================================================
# Simple Blog List style - begin
========================================================================== */
.qode-simple-blog-list {

	.qode-sbl-posts-holder {
		margin: 0 -17px;
	}
	.qode-sbl-post {
		position: relative;
		box-sizing: border-box;
		margin: 0;
		float: left;
		padding: 0 17px 30px;
		width: 33.33%;

		&:nth-child(3n+1){
			clear: both;
		}

		.qode-sbl-post-title {
			margin-bottom: 17px;
		}

		.qode-sbl-post-excerpt {
			margin: 0 0 31px;
			padding: 0;
		}
		.qode-sbl-category {
			position: relative;
			margin-bottom: 14px;
			padding-left: 44px;

			&:before {
				content: ' ';
				position: absolute;
				display: inline-block;
				width: 34px;
				height: 2px;
				top: 50%;
				left: 0;
				background-color: currentColor;
			}
		}
	}

	&.qode-sbl-4-columns {
		.qode-sbl-post {
			width: 25%;


			&:nth-child(3n+1){
				clear: none;
			}

			&:nth-child(4n+1){
				clear: both;
			}

		}
	}

	&.qode-sbl-5-columns {
		.qode-sbl-post {
			width: 20%;

			&:nth-child(3n+1){
				clear: none;
			}
			&:nth-child(5n+1){
				clear: both;
			}

		}
	}

	// Animation styles

	&.qode-sbl-animated {
		.qode-sbl-post {
			visibility: hidden;
		}

		.no-touch & {
			.qode-sbl-post {
				visibility: visible;
				opacity: 0;
				@include qodeTransform(translateY(50px));

				&.qode-appear{
					opacity: 1;
					@include qodeTransform(translateY(0));
					@include qodeTransition(all .6s cubic-bezier(0.34,0.52,0.57,1.04));

					&.qode-appeared{
						@include qodeTransition(none);
					}
				}
			}
		}

		.touch &.qode-sbl-post {
			visibility: visible;
			opacity: 1;
			@include qodeTransform(translateY(0));
		}
	}

}
/* ==========================================================================
# Simple Blog List style- end
========================================================================== */