/* ==========================================================================
# Centered Portfolio Carousel style - begin
========================================================================== */
.qode-centered-portfolio-carousel {

	.qode-cpc-post-image {

		a {
			display: block;
			position: relative;
		}

		.qode-cpc-overlay {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: #000;
			opacity: 0.2;
			z-index: 10;
			@include qodeTransition(opacity 0.3s ease-in-out);
		}
	}
	.owl-item.center {
		.qode-cpc-overlay {
			opacity: 0;
		}
	}

	&.qode-owl-slider .owl-nav {
		.owl-prev {
			left: 20px;
		}
		.owl-next {
			right: 20px;
		}
	}

}
/* ==========================================================================
# Centered Portfolio Carousel style - end
========================================================================== */